// =============================================================================
// X402 METRICS API
// =============================================================================
// Public API endpoint for x402 health and metrics
// Feeds the public metrics dashboard

import { NextRequest, NextResponse } from 'next/server';
import { getX402CallLogs } from '@/integrations/x402/x402-call-log';
import { listUsageLogs, listEndpoints } from '@/db/metered-endpoints';
import { getEndpoint } from '@/db/metered-endpoints';
import { getCDPFacilitator } from '@/integrations/x402/cdp-facilitator';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'X402Metrics' });

/**
 * GET /api/x402/metrics
 * 
 * Returns x402 system health and metrics
 * 
 * Query params:
 * - endpointId: Filter by specific endpoint (optional)
 * - hours: Time window in hours (default: 24)
 */
export async function GET(request: NextRequest) {
  try {
    const searchParams = request.nextUrl.searchParams;
    const endpointId = searchParams.get('endpointId') || undefined;
    const hours = parseInt(searchParams.get('hours') || '24', 10);
    
    // Calculate time window
    const now = new Date();
    const startDate = new Date(now.getTime() - hours * 60 * 60 * 1000).toISOString();
    
    // Get usage logs with safe defaults
    let usageLogs: Array<{ timestamp: string; statusCode: number; responseTime: number; failureCode?: string }> = [];
    try {
      const logsResult = await listUsageLogs({ endpointId });
      usageLogs = Array.isArray(logsResult) ? logsResult : [];
    } catch (error) {
      logger.warn('Failed to fetch usage logs', { error, endpointId });
      usageLogs = [];
    }
    
    if (!Array.isArray(usageLogs)) {
      logger.warn('Usage logs is not an array', { type: typeof usageLogs, endpointId });
      usageLogs = [];
    }
    
    const recentLogs = usageLogs.filter(log => log && log.timestamp && log.timestamp >= startDate);
    
    // Get x402 call logs for facilitator breakdown with safe defaults
    let x402Logs: Array<{ facilitator?: string }> = [];
    try {
      if (endpointId) {
        const logsResult = await getX402CallLogs(endpointId, { startDate });
        x402Logs = Array.isArray(logsResult) ? logsResult : [];
      } else {
        // Get all endpoints and fetch logs for each
        const endpointsResult = await listEndpoints();
        const endpoints = Array.isArray(endpointsResult) ? endpointsResult : [];
        
        if (endpoints.length === 0) {
          logger.debug('No endpoints found for metrics query');
          x402Logs = [];
        } else {
          const logPromises = endpoints.map(async (ep) => {
            try {
              const endpointLogs = await getX402CallLogs(ep.id, { startDate, limit: 1000 });
              return Array.isArray(endpointLogs) ? endpointLogs : [];
            } catch (error) {
              logger.warn('Failed to fetch x402 logs for endpoint', { endpointId: ep.id, error });
              return [];
            }
          });
          
          const logArrays = await Promise.all(logPromises);
          x402Logs = logArrays.flat().filter(log => log != null);
        }
      }
    } catch (error) {
      logger.error('Failed to fetch x402 call logs', { error, endpointId });
      x402Logs = [];
    }
    
    if (!Array.isArray(x402Logs)) {
      logger.warn('X402 logs is not an array', { type: typeof x402Logs, endpointId });
      x402Logs = [];
    }
    
    // Calculate metrics with safe defaults
    const totalRequests = recentLogs.length;
    const successfulRequests = recentLogs.filter(log => log && log.statusCode === 200).length;
    const failedRequests = recentLogs.filter(log => log && log.statusCode >= 400).length;
    const paymentRequiredRequests = recentLogs.filter(log => log && log.statusCode === 402).length;
    
    // Facilitator breakdown with safe iteration
    const facilitatorCounts: Record<string, number> = {};
    if (Array.isArray(x402Logs)) {
      x402Logs.forEach(log => {
        if (log && log.facilitator) {
          facilitatorCounts[log.facilitator] = (facilitatorCounts[log.facilitator] || 0) + 1;
        }
      });
    }
    
    // Error breakdown with safe iteration
    const errorCounts: Record<string, number> = {};
    if (Array.isArray(recentLogs)) {
      recentLogs.forEach(log => {
        if (log && log.failureCode) {
          errorCounts[log.failureCode] = (errorCounts[log.failureCode] || 0) + 1;
        }
      });
    }
    
    // Average response time with safe defaults
    const avgResponseTime = recentLogs.length > 0 && Array.isArray(recentLogs)
      ? recentLogs.reduce((sum, log) => {
          const responseTime = log && typeof log.responseTime === 'number' ? log.responseTime : 0;
          return sum + responseTime;
        }, 0) / recentLogs.length
      : 0;
    
    // P95 response time with safe defaults
    const sortedResponseTimes = Array.isArray(recentLogs)
      ? recentLogs
          .map(log => log && typeof log.responseTime === 'number' ? log.responseTime : 0)
          .filter(rt => rt >= 0)
          .sort((a, b) => a - b)
      : [];
    const p95Index = sortedResponseTimes.length > 0 
      ? Math.floor(sortedResponseTimes.length * 0.95) 
      : 0;
    const p95ResponseTime = sortedResponseTimes[p95Index] || 0;
    
    // Check CDP health
    let cdpHealth = { healthy: false, error: 'Not checked' };
    try {
      const cdp = getCDPFacilitator();
      cdpHealth = await cdp.healthCheck();
    } catch (error) {
      cdpHealth = { 
        healthy: false, 
        error: error instanceof Error ? error.message : 'Health check failed' 
      };
    }
    
    // Get endpoint info if specified
    let endpointInfo = null;
    if (endpointId) {
      const endpoint = await getEndpoint(endpointId);
      if (endpoint) {
        endpointInfo = {
          id: endpoint.id,
          name: endpoint.name,
          status: endpoint.status,
          price: endpoint.price,
          network: endpoint.network,
        };
      }
    }
    
    return NextResponse.json({
      timestamp: now.toISOString(),
      timeWindow: {
        hours,
        startDate,
        endDate: now.toISOString(),
      },
      health: {
        cdp: cdpHealth,
        verifyMode: process.env.X402_VERIFY_MODE || 'off',
      },
      metrics: {
        totalRequests,
        successfulRequests,
        failedRequests,
        paymentRequiredRequests,
        successRate: totalRequests > 0 ? (successfulRequests / totalRequests) * 100 : 0,
        avgResponseTime: Math.round(avgResponseTime),
        p95ResponseTime: Math.round(p95ResponseTime),
      },
      breakdown: {
        byFacilitator: facilitatorCounts,
        byError: errorCounts,
      },
      endpoint: endpointInfo,
    });
  } catch (error) {
    const errorEndpointId = request.nextUrl.searchParams.get('endpointId') || undefined;
    logger.error('Failed to fetch x402 metrics', { 
      error: error instanceof Error ? error.message : String(error),
      stack: error instanceof Error ? error.stack : undefined,
      endpointId: errorEndpointId,
    });
    
    return NextResponse.json(
      {
        error: 'Failed to fetch metrics',
        message: error instanceof Error ? error.message : 'Unknown error',
        timestamp: new Date().toISOString(),
      },
      { status: 500 }
    );
  }
}



